<template>
    <div class="c-authorposts">
        <div class="u-label">
            <i class="el-icon-s-management"></i>
            <span>作者最新作品</span>
            <a :href="author_link" class="u-more">全部 &raquo;</a>
        </div>
        
        <ul v-if="data.length">
            <li v-for="(item, i) in data" :key="i">
                <a
                    class="u-item"
                    :href="url(item.post.ID, item.post.post_type)"
                    target="_blank"
                >
                    <!-- <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" aria-hidden="true" fill="#555" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 .55-.45 1-1 1H6v2l-1.5-1.5L3 16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 1-1h10c.55 0 1 .45 1 1zm-1 10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"/></svg> -->
                    <span>&raquo; {{ item.post.post_title }}</span>
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
import { __Root } from "@jx3box/jx3box-common/js/jx3box.json";
import { getUserPosts } from "../service/author";
export default {
    name: "Authorposts",
    props: ["uid"],
    data: function() {
        return {
            data: [],
        };
    },
    methods: {
        author_link : function (){
            return __Root + 'author/?uid=' + this.uid  
        },
        url: function(id, type) {
            return __Root + type + "/" + id;
        },
    },
    mounted: function() {
        if (!this.uid) return;
        getUserPosts(this.uid).then((res) => {
            this.data = res.data.data.list.slice(0, 6);
        });
    },
};
</script>

<style lang="less">
@import "../assets/css/authorposts.less";
</style>
